import React, { Component } from 'react'

export class Earth extends Component {
    state={
        loading: true,
        nasaData: null,    
      }
    //   https://epic.gsfc.nasa.gov/api/natural 
      async componentDidMount() {
        const url = 'https://api.nasa.gov/EPIC/api/natural/images?api_key=s3Ro78SfpZQASJOPCYH3Ggcoby7guKm2mVaqoDgq'
        const response = await fetch(url)
        const data = await response.json()
        this.setState({
          nasaData: data[0], loading: false
        })
        console.log(data)
      }
      render() {
    
        return (
          <div className={`App`}>        
            <div>{ this.state.loading || !this.state.nasaData ? (
                <div>
                  Loading...
                </div>
              ):(
                <div>
                    
                    ...data
                  <div><h1>Earth</h1></div>
                  <div><img src='https://api.nasa.gov/EPIC/archive/natural/2019/05/30/png/epic_1b_20190530011359.png?api_key=s3Ro78SfpZQASJOPCYH3Ggcoby7guKm2mVaqoDgq' alt='' /></div>
                  <div><h3>{this.state.nasaData.caption}</h3></div>
                </div>
            )}</div>
          </div>
        )
      }
}

export default Earth
